import { axiosInstance } from "../../../axios-Instance";
import { toast } from "react-toastify";
import { successAlert, toastOptions } from "../../../utils";
import { useMutation, useQueryClient } from "@tanstack/react-query";
import { queryKeys } from "./../../../react-query/constants";

const SERVER_ERROR = "There was an error contacting the server.";

async function createPartner(formData) {
  const data = await axiosInstance({
    url: "/partner",
    method: "POST",
    data: formData,
    headers: {
      "Content-Type": "application/json",
    },
  });

  return data?.data;
}

export function usePartner() {
  const queryClient = useQueryClient();
  const { mutate, isError, error, isSuccess, reset } = useMutation({
    mutationFn: (formData) => createPartner(formData),
    onSuccess: () => {
      queryClient.invalidateQueries([queryKeys.partner]);
      successAlert("Form Filled successfully");
    },
    onError: (error) => {
      const err = error?.response?.data?.error
        ? error?.response?.data?.error
        : SERVER_ERROR;
      toast.error(err, toastOptions);
    },
  });
  return { mutate, isError, error, isSuccess, reset };
}
