import { useIsMutating } from "@tanstack/react-query";
import { Button, FormGroup, Input, Select } from "mtforms";
import React, { useState } from "react";
import ReCAPTCHA from "react-google-recaptcha";
import Footer from "../../components/Footer";
import Header from "../../components/Header";
import "./style.css";
import { usePartner } from "./hooks";
import { useNavigate } from "react-router-dom";
import { errorAlert } from "../../utils";

const Home = () => {
  const [formData, setFormData] = useState({});
  const [errors, setErrors] = useState({});
  const isLoading = useIsMutating();
  const [isCaptchaVerified, setIsCaptchaVerified] = useState(false);
  const navigate = useNavigate();
  const handleChange = (name, value) => {
    setFormData({ ...formData, [name]: value });
  };
  const validationHandler = (name, error) => {
    setErrors({ ...errors, [name]: error });
  };

  const handleCaptchaChange = (value) => {
    setIsCaptchaVerified(true);
  };

  const bool = [
    { boolanValue: "YES", value: true },
    { boolanValue: "NO", value: false },
  ];

  const { mutate, isError, error, isSuccess, reset } = usePartner();
  const handleSubmit = () => {
    mutate(formData);
  };

  if (isSuccess) {
    reset();
    setFormData({});
    navigate("/thank-you");
  }
  if (isError) {
    reset();
    errorAlert(error);
  }

  return (
    <div>
      <Header />
      <div className="homepage-container">
        <h2>Sign Up</h2>
        <FormGroup
          onSubmit={handleSubmit}
          errors={errors}
          setErrors={setErrors}
        >
          <div className="form_container">
            <Input
              name="name"
              label="Name"
              value={formData["name"]}
              onChange={handleChange}
              type="text"
              validationHandler={validationHandler}
              error={errors.name}
              required={true}
              readOnly={true}
              size="large"
              className="formInput"
            />
            <Input
              name="organization"
              label="Organization"
              value={formData["organization"]}
              onChange={handleChange}
              type="text"
              validationHandler={validationHandler}
              error={errors.organization}
              required={true}
              readOnly={true}
              size="large"
              className="formInput"
            />
            <Input
              name="role"
              label="Role"
              value={formData["role"]}
              onChange={handleChange}
              type="text"
              validationHandler={validationHandler}
              error={errors.role}
              required={true}
              readOnly={true}
              size="large"
              className="formInput"
            />
            <Input
              name="email"
              label="Email"
              value={formData["email"]}
              onChange={handleChange}
              type="text"
              validationHandler={validationHandler}
              error={errors.email}
              required={true}
              readOnly={true}
              size="large"
              className="formInput"
            />
            <Input
              name="phone"
              label="Phone (+ Dialing Code) "
              value={formData["phone"]}
              onChange={handleChange}
              type="text"
              validationHandler={validationHandler}
              error={errors.phone}
              required={true}
              readOnly={true}
              size="large"
              className="formInput"
            />
            <Select
              name="isExistingSubscriber"
              label="Are you an Existing AWS Subscriber?"
              value={formData["isExistingSubscriber"]}
              onChange={handleChange}
              validationHandler={validationHandler}
              error={errors.isExistingSubscriber}
              required={true}
              filter="boolanValue"
              filterValue="value"
              data={bool}
              size="large"
              className="formInput"
            />
            <Select
              name="isCurrentlyOnProject"
              label="Are you currently on a project?"
              value={formData["isCurrentlyOnProject"]}
              onChange={handleChange}
              type="text"
              validationHandler={validationHandler}
              error={errors.isCurrentlyOnProject}
              required={true}
              filter="boolanValue"
              filterValue="value"
              data={bool}
              size="large"
              className="formInput"
            />
            <Input
              name="monthlyConsumption"
              label="What is your monthly consumption ($)"
              value={formData["monthlyConsumption"]}
              onChange={handleChange}
              type="number"
              validationHandler={validationHandler}
              error={errors.monthlyConsumption}
              required={true}
              readOnly={true}
              size="large"
              className="formInput"
            />
            <Input
              name="earliestAvailability"
              label="Please indicate your earliest  
            availabilty for a call"
              value={formData["earliestAvailability"]}
              onChange={handleChange}
              type="date"
              validationHandler={validationHandler}
              error={errors.earliestAvailability}
              required={true}
              readOnly={true}
              size="large"
              className="formDate"
            />

            <ReCAPTCHA
              sitekey="6LflLzwnAAAAAGIWo__cNuvdsJZ007eodOXVADp0"
              onChange={handleCaptchaChange}
            />

            <Button
              size="large"
              type="submit"
              title={isLoading ? "loading.." : "Sign Up"}
              disabled={!isCaptchaVerified || isLoading}
              className="formButton"
            />
            <Button
              size="large"
              onClick={() => {
                setFormData({});
              }}
              type="button"
              title={isLoading ? "loading.." : "Cancel"}
              disabled={isLoading ? true : false}
              className="btn btnWhite"
              style={{ cursor: "pointer" }}
            />
          </div>
        </FormGroup>
      </div>
      <Footer />
    </div>
  );
};

export default Home;
